body {
    margin: 0px;
}

#app {
    background-color: #000000;
    height: 100vh;
    display: grid;
    grid-template-rows: 70px 50px auto 70px;
}

.Header {
    background-color: #202020;
    border: solid #303030 4px;
    border-bottom: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 10px;
}

.Logo {
    color: white;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 40px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Menu {
    height: 100%;
    background-color: #181818;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    display: grid;
    grid-template-columns: 40px -webkit-min-content -webkit-min-content 40px 40px 40px;
    grid-template-columns: 40px min-content min-content 40px 40px 40px;
    gap: 4px;
    padding: 4px;
}

.menuAvatar {
    background-image: url('https://steamuserimages-a.akamaihd.net/ugc/885384897182110030/F095539864AC9E94AE5236E04C8CA7C2725BCEFF/');
    background-position: center;
    background-size: cover;
    cursor: pointer;
}

.menuName {
    color: white;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 25px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: rgba(0, 0, 0, 0.274);
    padding-left: 10px;
    padding-right: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.menuBalance {
    color: white;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 25px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: rgba(0, 0, 0, 0.274);
    padding-left: 10px;
    padding-right: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.fa-coins {
    margin-right: 4px;
}

.menuBTN {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 25px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: rgba(0, 0, 0, 0.274);
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
    border: none;
    color: #303030;
    outline: none;
}

.menuBTN:hover {
    color: white;
}

.gameModes {
    background-color: #303030;
    padding: 4px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 4px;
}

.gameMode {
    background-color: #202020;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    color: #303030;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 25px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding-right: 10px;
    padding-left: 10px;
    cursor: pointer;
}

.gameModeActive {
    background-color: #181818;
    color: white;
}

.gameMode:hover {
    background-color: #181818;
    color: white;
}

.Content {
    display: grid;
    grid-template-columns: 250px auto;
}

.sidebarLeft {
    background-color: #202020;
    border-right: solid #303030 4px;
    border-left: solid #303030 4px;
    display: grid;
    grid-template-rows: auto 50px;
    gap: 4px;
    padding: 4px;
}

.Chat {
    background-color: #181818;
    display: grid;
    grid-template-rows: 1fr;
    overflow-y: scroll;
}

.Chat::-webkit-scrollbar { 
    display: none; 
}

.innerChat {
    background-color: transparent;
    display: grid;
    grid-auto-rows: -webkit-min-content;
    grid-auto-rows: min-content;
}

.chatMessage {
    background-color: transparent;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 15px;
    color: white;
    display: grid;
    grid-template-rows: 30px -webkit-min-content;
    grid-template-rows: 30px min-content;
    padding: 4px;
}

.chatMessage:nth-child(odd) {
    background-color: rgba(0, 0, 0, 0.075);
}

.User {
    border-bottom: solid #181818 4px;
    background-color: rgba(0, 0, 0, 0.274);
    padding: 4px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}

.postedTime {
    font-size: 12px;
}

.Message {
    background-color: rgba(0, 0, 0, 0.274);
    padding: 4px;
    overflow: hidden;
    white-space: wrap;
    font-size: 15px;
}

.chatSend {
    background-color: #181818;
    display: grid;
    grid-template-columns: auto 30px;
    gap: 4px;
    padding: 4px;
}

.chatInp {
    background-color: transparent;
    color: white;
    border: none;
    outline: none;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 15px;
}

.chatBtn {
    background-color: transparent;
    color: #303030;
    border: none;
    font-size: 20px;
    cursor: pointer;
    outline: none;
}

.chatBtn:hover {
    color: white;
}

.Display {
    background-color: #181818;
    border-right: solid #303030 4px;
}

.gameIcon {
    background-color: black;
}

.Footer {
    background-color: #202020;
    border: solid #303030 4px;
}

.underConstruction {
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.fa-tools , .fa-bug {
    color: white;
    font-size: 150px;
    border: solid #303030 4px;
    border-radius: 150px;
    text-shadow: 0 1px 0 #303030, 0 2px 0 #303030, 0 3px 0 #303030, 0 4px 0 #303030, 0 5px 0 #303030, 0 6px 0 #303030, 0 7px 0 #303030, 0 8px 0 #303030, 0 9px 0 #303030, 0 50px 30px rgba(0, 0, 0, 0.3);
    -webkit-animation: rotate-center 5s ease-in-out infinite both;
    animation: rotate-center 5s ease-in-out infinite both;
}

@-webkit-keyframes rotate-center {
    0% {
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes rotate-center {
    0% {
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

.fade-enter-active, .fade-leave-active {
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
  }

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
  
  